var mStyle = {
	li: {
		display: 'inline',
		padding: '2px',
    paddingRight: '4px',
		backgroundColor: '#FFFFCC'
	},
	ul: {
	},
  span:{
    color: 'yellow'
  },
  div: {
    height: '900',
    background: 'green'
  },
  a: {
  	textDecoration: 'none',
  	color: 'green'
  }
};

const pStyle= {
  outer: {
    display: 'flex',
    flexDirection: 'column',
    background:'#C4A265',
    height: 400,
    textAlign: 'center'    
  },
  inner: {
    margin: '0 auto',
    display: 'flex',
    flexDirection: 'column',
    background: '#FFF28E',
    height: 340,
    color: 'red',
    textAlign: 'center',
    fontSize: '300%'
  }
}

export {mStyle, pStyle}


